<template>
  <div class="container">
      <table class="table table-light">
        <thead>
            <tr>
            <th scope="col">Meilleurs Scores</th>
            <th scope="col">Nom</th>
            <th scope="col">Points</th>
            </tr>
        </thead>
        <tbody v-for="(score, index) in Scores" :key="index" id="table">
            <tr class="border-bottom">
            <th scope="row">{{index+1}}</th>
            <td class="border-bottom">{{score.nom}}</td>
            <b><td>{{score.Score}}</td></b>
            </tr>
        </tbody>
</table>
  </div>
</template>

<script>
import axios from 'axios'

export default {
    name: "Table",
    data() {
		return {
			Scores: []
		}
    },
    async created() {
    const res = await axios.get('api/score');
    const data = res.data;
    const top10 = data.sort((a,b) =>  b.Score-a.Score)
    this.Scores = top10.slice(0, 10)
    this.$emit('Table', top10.slice(9, 10))
	},
}
</script>

<style scoped>
    #table{
        transition: all 1.3s ease-out
    }
</style>